h1 {
    cursor: pointer;
    position: relative;
    z-index: 1;
  }
  
  .header {
    display: flex;
    align-items: center; 
    justify-content: center;
    margin-top: 50px;
  }
  
  .header img {
    width: 50px;
    height: 50px;
    margin-right: 20px;
    border-radius: 50%;
    object-fit: cover;
    align-self: center; 
    position: relative;
    transform: translateY(-50px);
  }
  
  .header h1 {
    font-size: 48px;
  }


  .photo-container {
    position: relative;
    display: flex;
    justify-content: center;
  }
  
  .photo-container img {
    max-width: 30%;
    max-height: 200px;
    position: absolute;
    top: 0;
    left: 0;
    margin: 0px;
    transition: transform 0.5s ease-out, opacity 0.5s ease-out;
    animation: move 3s ease-in-out infinite alternate;
  }

  @keyframes move {
    0% {
      transform: translateX(-10px) translateY(10px);
    }
    100% {
      transform: translateX(10px) translateY(-10px);
    }
  }


  .photo-container img:hover {
    transform: scale(1.1);
  }


  .photo-container img:nth-child(1) {
    top: 50%;
    left: 0%;
  }
  
  .photo-container img:nth-child(2) {
    top: 5%;
    left: 37.5%;
  }
  
  .photo-container img:nth-child(3) {
    top: 100%;
    left: 82%;
  }
  
  .photo-container img:nth-child(4) {
    top: 10%;
    left: 20%;
  }
  
  .photo-container img:nth-child(5) {
    top: 35%;
    left: 65%;
  }

  .logo-container {
    position: absolute;
    top: 10px;
    right: 100px;
  }

  .logo-container a {
    text-decoration: none;
  }
  
  
  .social-logo {
    width: 60px;
    height: 60px;
    margin-left: 0px;
    margin-right: 10px;
  }

  .big-text {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 30vh; 
    flex-direction: column; 
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-family: 'Roboto', sans-serif;
    margin-top: 20vh; 
  }
  
  #loader {
    color: #1B4F72;
    display: none;
    justify-content: center;
    align-items: center;
    height: 0vh; 
    flex-direction: column; 
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-family: 'Roboto', sans-serif;
    margin-top: 0vh; 
  }
  
  
  
  
  
  
  
  

  